// app/page.tsx
import os from 'node:os';

const getHostLabel = () => {
  const host = process.env.HOSTNAME || os.hostname();
  if (!host) return 'unknown';
  return host.includes('localhost') ? 'localhost' : host;
};

const HomePage = async () => {
  const hostLabel = getHostLabel();

  return (
    <main className="flex min-h-screen items-center justify-center bg-slate-50">
      <p className="rounded-xl bg-white px-6 py-4 text-lg font-semibold text-slate-800 shadow">
        {hostLabel}
      </p>
    </main>
  );
};

export default HomePage;